<template>
	<view class="content">
		<u-tabs :list="cats" :is-scroll="true" :current="current" @change="change" count="cate_count"
			inactive-color="#999" :active-item-style="activeStyle" active-color="#d83632" class="u-tabs"></u-tabs>


		<view class="articleList">
			<view class="item" v-for="(item,index) in posts" :key="index" @click="gotoLink(item.link)">
				<view class="normal-post">
					<view class="post-content">
						<view class="cover">
							<image :src="item.thumbnail" mode="aspectFill"></image>
						</view>

						<view class="detail">
							<view class="title">
								{{item.title}}
							</view>
							<view class="info">
								<view class="left">
									<text class="red-border">{{item.channelName}}</text>
								</view>
								<view class="righr">
									<text class="time">{{item.date}}</text>
								</view>
							</view>

						</view>
					</view>
				</view>

			</view>
		</view>

		<u-loadmore :status="status" />
	</view>
</template>

<script>
	import {
		Request
	} from '@/api/request.js'
	export default {
		data() {
			return {
				activeStyle: {
					'font-size': '38rpx',
					'color': '#d83632'
				},
				status: "loading",
				current: 0,
				cats: [{
						categoryId: 0,
						name: '全部',
						except: true
					},
					{
						categoryId: 1,
						name: '党史',
						except: false
					}
				],
				posts: [],
			}
		},
		methods: {
			getCats() {
				Request({
						url: "learn/getCats",
						method: "get"
					}).then(res => {
						this.cats = res.data;
						console.log(res);
					})
					.catch(cat => {
						console.log(cat);
					});
			},
			change: function(e) {
				this.current = e;
				this.id = this.cats[e].id;
				this.posts = [];
				this.status = "loading";
				this.getPostsById(e + 1);
			},
			getPostsById(id) {
				Request({
						url: "learn/getPostsById",
						method: "post",
						data: {
							"learnId": id
						}
					}).then(res => {
						this.status = "";
						this.posts = res.data;
						console.log(res);
					})
					.catch(cat => {
						console.log(cat);
					});
			},
			gotoLink(link) {
				console.log(link)
				// plus.runtime.openURL(link);
				// window.location.href = link;
				uni.navigateTo({
					url: '/pages/webview/webview?url=' + link
				})
			}
		},
		onLoad() {
			this.getCats();
			this.getPostsById(1);
		}
	}
</script>

<style lang="scss" scoped>
	.content {
		background-color: whitesmoke;
		padding-bottom: 30rpx;
	}

	.articleList {


		.item {
			padding: 0 30rpx;
			margin-bottom: 30rpx;
			border-bottom: 1rpx solid #f5f5f5;
			background-color: white;

			.normal-post {
				display: block;
				box-sizing: content-box;
				padding-top: 40rpx;
				position: relative;
				min-height: 180rpx;

				.post-content {
					display: flex;

					.detail {
						padding-left: 40rpx;

						.title {
							padding-top: 10rpx;
							line-height: 52rpx;
							font-size: 36rpx;
							color: #000;
							white-space: normal;
							word-break: normal;
							word-wrap: break-word;
							-webkit-hyphens: auto;
							-ms-hyphens: auto;
							hyphens: auto;
							display: -webkit-box;
							-webkit-box-orient: vertical;
							-webkit-line-clamp: 1;
							overflow: hidden;
						}

						.info {
							margin-top: 36rpx;
							height: 38rpx;
							display: flex;

							text {
								display: inline-block;
								vertical-align: top;
								height: 32rpx;
								line-height: 32rpx;
								font-size: 24rpx;
								color: #969696;
								margin-right: 10rpx;
							}

							.red-border {
								display: block;
								background-color: #ffe7dd;
								color: #ff5b60;
								padding: 0 20rpx;
								border-radius: 20rpx;
							}
						}

					}

					.cover {
						-o-object-fit: cover;
						object-fit: cover;


						image {
							width: 160rpx;
							height: 160rpx;
							border-radius: 4px;
						}
					}

				}
			}

		}
	}
</style>
